import React from 'react'
import {Link} from 'react-router-dom'
import Footer from './Footer'
import Pimages from './ProductsImg'
import './comcss/Products.css'
export default function Products() {
document.title="F-CARS | Products"
  return (
    <div>
      <div className='container'>

        <div className='row d-flex flex-row justify-content-center'>

          {
            Pimages.map((items) => <div className='col-md-5 p-5 brands'><Link to={`/allCars/${items.name}`}><img src={items.path} alt='' className='Pimages'/></Link>
            <h3>{items.name.toLocaleUpperCase()}</h3>
            </div>)
          }

        </div>



      </div>

      <Footer />
    </div>
  )
}
