import React, { useEffect, useState } from 'react'
import { Link ,useNavigate} from 'react-router-dom'
import "./comcss/NavBar.css"
const ham = require('../images/ham.png')
const logo = require('../images/logo.jpg')
export default function NavBar() {
  let navigate=useNavigate()
useEffect(()=>{
  navigate("/")
},[])
  const [change, setChange] = useState(false)
  const [hambtn, setHam] = useState(false)
  return (
    <div className='main'>
      <div className='mainNav'>
        <div className='container' >

          <div className='row' >

        <img src={logo} alt='' className=' logo col-1' />
            <div className='col-3 mt-1'>
              <h1 id='logo'>F-CARS</h1>
            </div>
            <div className='col-md-8 deskPages'>
              <ul className='navItems float-md-end'>
                <li><Link to={'/'}>Home</Link></li>
                <li><Link to={'/gallery'}>Gallery</Link></li>
                <li><Link to={'/products'}>Products</Link></li>
                <li><Link to={'/about'}>About</Link></li>
              </ul>
            </div>



          </div>
        </div>
        {

          change && <div className='resDiv'>
            <ul className='resAnchors container'>
              <li><Link to={'/'}>Home</Link></li>
              <li><Link to={'/gallery'}>Gallery</Link></li>
              <li><Link to={'/products'}>Products</Link></li>
              <li><Link to={'/about'}>About</Link></li>
            </ul>
          </div>
        }
      </div>
      <button onClick={() => {setChange(!change)
                             setHam(!hambtn) }     
      } className='hambtn'><img src={ham} alt='' className={hambtn&&`hamImg`}/></button>

    </div>
  )
}
