import React from 'react'
import {Link, useParams} from 'react-router-dom'
import Cars from './CarsProducts'
import './comcss/AllCars.css'
export default function AllCars() {
    let params=useParams().id
    let pics=''
    if(params==='corolla'){
        pics=Cars.corolla
    }
    else if(params==='prius'){
        pics=Cars.prius
    }
    else if(params==='mercedes'){
        pics=Cars.mercedes
    }
    else if(params==='mustang'){
        pics=Cars.mustang
    }
    else if(params==='prius'){
        pics=Cars.prius
    }
    else if(params==='prado'){
        pics=Cars.prado
    }
    else if(params==='toyota'){
        pics=Cars.toyota
    }



    document.title=`F-CARS | Products-${params}`
    return (
    <div>
      <div className='container'>
            <button id='goBackBtn'><Link to={'/products'}>Go Back</Link></button>
        <div className='row d-flex flex-row justify-content-center'>
          {
            pics.map((items)=><div className='col-md-5 p-3 brands'><img src={items.path} alt='' className='Pimages'/>
            <h3>PRICE: PKR-{items.price}</h3>
            </div>)
          }
        </div>
      </div>
    </div>
  )
}
