import React from 'react'
import Footer from './Footer'
import GalleryPics from './GalleryPics'
import './comcss/Gallery.css'
export default function Gallery() {

    document.title="F-CARS | Gallery"
  return (
    <div>
        <marquee className='marquee'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Nisi suscipit possimus fuga voluptates harum aliquid ducimus ipsum atque nulla necessitatibus!</marquee>
        <div className='container'>
        
          <div className='row'>
          
           {
            GalleryPics.map((items)=><div className='col-md-5 p-5'><img src={items.path} className='galleryImages' alt=''/></div>)
           }
           
          </div>
     
          
        
        </div>
        <Footer/>
    </div>
  )
}
